.SearchForm-input {
	display: inline-block;
	width: 100%;
	font: inherit;
	font-size: 20px;
	border: none;
	outline: none;
	padding-left: 4px;
	padding-right: 4px;
	border: 1px black solid;
	width: 230px;
	padding-bottom: 2px;
}
.SearchForm-button-label {
	position: absolute;
	width: 1px;
	height: 1px;
	padding: 0;
	overflow: hidden;
	clip: rect(0, 0, 0, 0);
	white-space: nowrap;
	clip-path: inset(50%);
	border: 0;
}
.SearchForm-button:hover {
	opacity: 1;
}
.SearchForm-input::placeholder {
	font: inherit;
	font-size: 18px;
}
.SearchForm-button {
	display: inline-block;
	width: 48px;
	height: 26px;
	border: 0;
	background-image: url('https://image.flaticon.com/icons/svg/149/149852.svg');
	background-size: 40%;
	background-repeat: no-repeat;
	background-position: center;
	opacity: 0.6;
	transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
	cursor: pointer;
	outline: none;
	padding-top: 12px;
}
.Searchbar {
	z-index: 1100;
	display: flex;
	padding-right: 24px;
	padding-left: 24px;
	padding-top: 12px;
	padding-bottom: 12px;
	width: 300px;
	color: #fff;
	background-color: #3f51b5;
	box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
		0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}
